<template>
  <div class="body">
<!--    Breast Cancer-Predicting 5-Year Risk of Arm Lymphedema-->
    <div class="title">Arm Lymphatics Distribution model-Predicting Risk of Arm Lymphedema</div>
    <div class="content">
      <div class="top">
        <el-form :model="form" :rules="rules" ref="form" label-position="top" label-width="calc(100px + .5vw)" class="demo-ruleForm">
          <el-form-item label="BMI" prop="BMI" class="label-class" :*required="false" >
            <el-input v-model.number="form.BMI"></el-input>
          </el-form-item>
          <el-form-item label="Chemotherapy" prop="Chemotherapy">
            <el-select v-model="form.Chemotherapy" placeholder="Please Choose Chemotherapy Type" style="width: 100%">
              <el-option label="Chemotherapy with Taxanes" value="26.37667"></el-option>
              <el-option label="Chemotherapy without Taxanes" value="0.000"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="Radiotherapy" prop="Radiotherapy">
            <el-select v-model="form.Radiotherapy" placeholder="Please Choose Radiotherapy Type" style="width: 100%">
              <el-option label="Breast + Supraclavicular and infraclavicular " value="47.86854"></el-option>
              <el-option label="Supraclavicular fossa" value="23.93427"></el-option>
              <el-option label="No" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="Proportion of Arm Lymph Flow Above the Level of Axillary Vein" prop="ArmLyFlow" style="width: 100%">
            <el-input v-model="form.ArmLyFlow" ></el-input>
          </el-form-item>
<!--          <el-form-item>-->
<!--            <el-button @click="resetForm('form')" type="danger">重置</el-button>-->
<!--          </el-form-item>-->
        </el-form>
      </div>
      <div class="center">
        <el-button type="success" @click="calculate" style="float: left;margin-bottom: 1vh">Run Calculator</el-button>
        <el-table
                :data="tableData"
                :row-style="{width:'100%'}"
                :header-cell-style="{background: '#5e6364',color:'#fbfdfe'}"
                border v-if="showRes">
          <el-table-column
                  prop="result"
                  label="Result"
                  min-width="65%">
          </el-table-column>
          <el-table-column
                  prop="probability"
                  label="Probability"
                  min-width="35%">
          </el-table-column>
        </el-table>
      </div>
