<script>
export default {
  name: 'Main',
  data() {
    var validatePass = (rule, value, callback) => {
      let val = parseFloat(this.form.ArmLyFlow)
      if (val < 0 || val > 1 )  {
        callback(new Error('Range 0-1'));
      } else {
        callback()
      }
    };
    return {
      tableData: [{
        result: '',
        probability: ''
      }],
      showRes: false,
      res: "",
      form: {
        BMI: 0,
        Chemotherapy: '',
        Radiotherapy: '',
        ArmLyFlow: 0
      },
      rules: {
        BMI: [
          { type: 'number', min: 0, max: 100, required: true, message: 'Range:0-100', trigger: 'blur' }
        ],
        Chemotherapy: [
          { required: true, message: 'Please Select the Type of Chemotherapy ', trigger: 'blur' }
        ],
        Radiotherapy: [
          { required: true, message: 'Please Select the Type of Radiotherapy', trigger: 'blur' }
        ],
        ArmLyFlow: [
          { validator: validatePass, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    calculate () {
      this.$refs["form"].validate((valid) => {
        this.$message(this.form.ArmLyFlow)
        if (valid) {
          console.log("BMI is" + this.form.BMI)
          console.log("Chemotherapy is " + this.form.Chemotherapy)
          console.log("Radiotherapy is " + this.form.Radiotherapy)
          console.log("ArmLyFlow" + this.form.ArmLyFlow)
          let y1 = 4.545454545 * parseFloat(this.form.BMI) - 72.727273
          console.log("y1 is: " + y1)
          let y2 = parseFloat(this.form.Chemotherapy)
          console.log("y2 is: " + y2)
          let y3 = parseFloat(this.form.Radiotherapy)
          console.log("y3 is: " + y3)
          let y4 = -81.450929875 * parseFloat(this.form.ArmLyFlow) + 73.305836888
          console.log("y4 is: " + y4)
          let points = y1 + y2 + y3 + y4
          console.log("points is: " + points)
          let p = (-1.128 * Math.pow(10, -6) * Math.pow(points, 3) + 0.000574661 * Math.pow(points, 2)
                  - 0.08688959 * points + 4.200209846) * 100
          this.tableData[0].result = 'Intraoperatively predicted arm lymphedema probability'
          this.tableData[0].probability = p.toFixed(2) + "%"
          this.showRes = true
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

